body {
  background-image: url("bg-img.6c5f455d.jpg");
  font-family: Dancing Script, cursive;
}

.cat-info {
  font-size: 30px;
}

@keyframes changeWrap {
  0% {
    opacity: 0;
    scale: .5;
  }

  100% {
    opacity: 1;
    scale: 1;
  }
}

@keyframes imgChange {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

img {
  border: 3px solid teal;
  animation: imgChange .45s linear;
}

.wrap {
  animation: changeWrap .5s linear;
}

.content {
  flex-direction: column;
  font-size: 70px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 3px dashed #ff3d00;
  border-bottom: 3px solid rgba(0, 0, 0, 0);
  border-left: 3px dotted #ff3d00;
  border-right: 3px solid #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  border: 10px solid rgba(0, 0, 0, 0);
  border-right-color: #ff3d00;
  position: absolute;
  top: 31px;
  left: 20px;
  transform: rotate(-40deg);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.is-hidden {
  display: none;
}

